import React, { useState } from "react";
import { Typography, Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    background: "#e9ecef",
    padding: "10px",
    margin: "0px",
    maxWidth: "85%",
  },
  Typography: {
    fontFamily: "Trocchi",
    alignSelf: "left",
  },
}));

const Footer = () => {
  const classes = useStyles();

  return (
    <Box className={classes.root} id="skills">
      <Typography variant="h4" className={classes.Typography}>
        Skills:
      </Typography>
      <ul>
        <li>Python</li>
        <li>TensorFlow</li>
        <li>Keras</li>
        <li>OpenCV</li>
        <li>scaPy</li>
        <li>Spark</li>
        <li>JavaScript</li>
        <li>Node.js</li>
        <li>ReactJS</li>
        <li>MongoDB</li>
        <li>Express</li>
        <li>SQL/PostgreSQL</li>
        <li>C/C++</li>
        <li>Numpy</li>
        <li>Pandas</li>
        <li>Seaborn/Matplotlib</li>
        <li>Librosa</li>
      </ul>
    </Box>
  );
};

export default Footer;
